.search-m .form-actions:before {
  display: inline-block;
  background-image: url("../img/sprite.svg");
  background-size: 25.1875em 22.625em;
}

.search-m {
  width: 100%;
  margin-top: 25px;
}

.search-m form {
  display: flex;
  position: relative;
}

.search-m .form-item {
  flex: 1;
  margin: 0;
}

.search-m .form-text {
  background-color: transparent;
  border: 1px solid #fff;
  padding: 11px 44px 10px 16px;
  border-radius: 0;
  color: #fff;
  font-size: 18px;
}

.search-m .form-actions {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  bottom: 0;
  width: 50px;
}

.search-m .form-actions:before {
  content: '';
  width: 1.3125em;
  height: 1.3125em;
  background-position: -23.5625em -16.625em;
  top: 0;
  right: 0;
  position: absolute;
  bottom: 0;
  left: 0;
  margin: auto;
}

.search-m .form-submit {
  position: relative;
  border: none;
  background-color: transparent;
  text-indent: -999999px;
  height: 100%;
  width: 100%;
  padding: 0;
  z-index: 1;
  cursor: pointer;
}